/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

.mkdf-pl-holder {
    @include mkdfRelativeHolderLayout();
	
	&.mkdf-info-on-image {
		
		.mkdf-pli {
			
			.mkdf-pli-price {
				padding-bottom: 12px;
			}
			
			.mkdf-pli-add-to-cart {
				margin: 15px 0 0;
			}
		}
	}

    .mkdf-pli {
        padding: 0;
        z-index: 100;

	    .mkdf-pli-title {
			opacity: 0;
			transform: translateY(10px);
			transition: .3s .15s;
			margin: 0 0 4px;
	    }
	
	    .mkdf-pli-category {
		    margin: 11px 0 3px;
	    }

        .mkdf-pli-excerpt {
            color: $default-heading-color;
	        line-height: 1.3em;
            margin: 11px 0 3px;
        }

	    .mkdf-pli-rating-holder {
		    @include mkdfRelativeHolderLayout();
		    line-height: 1;
		    margin: 10px 0 3px;
	    }

	    .mkdf-pli-rating {
		    @include mkdfWooCommerceRatingsStyle();
	    }

        .mkdf-pli-price {
			opacity: 0;
			transform: translateY(10px);
			transition: .3s .15s;
	        @include mkdfWooCommercePriceStyle();
        }
    }

    .mkdf-pli-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;
		overflow: hidden;

        &:hover {

            .mkdf-pli-text, .mkdf-pli-price, .mkdf-pli-title {
				opacity: 1;
				transform: translateY(0);
			}

			.mkdf-pli-text-inner .mkdf-pli-add-to-cart {
				opacity: 1;
				transform: translateY(0);
			}
			
			.mkdf-button {

				&:after {
					background-position: 20px 0 !important;
					transition: .3s .2s !important;
				}				
			}
        }

        .touch & {
            cursor: pointer;
        }

        .mkdf-pli-image {
            @include mkdfRelativeHolderLayout();

            img {
                display: block;
                width: 100%;
            }

            .mkdf-pli-onsale,
            .mkdf-pli-out-of-stock,
            .mkdf-pli-new-product {
	            @include mkdfWooCommerceMarksGeneralStyle();
            }

            .mkdf-pli-onsale {
	            @include mkdfWooCommerceSaleStyle();
            }

            .mkdf-pli-out-of-stock {
	            @include mkdfWooCommerceOutOfStockStyle();
            }
	
	        .mkdf-pli-new-product {
		        @include mkdfWooCommerceNewProductStyle();
	        }
        }
            
        .mkdf-pli-text {
            @include mkdfAbsoluteHolderLayout();
            padding: 10px;
            opacity: 0;
            background-color: rgba($default-background-color, 0.8);
            box-sizing: border-box;
			transition: .3s;
			pointer-events: none;
			z-index: 123;
        }

        .mkdf-pli-text-outer {
	        @include mkdfTableLayout();
			text-align: center;
        }

        .mkdf-pli-text-inner {
            @include mkdfTableCellLayout();

            .mkdf-pli-title a,
            .mkdf-pli-category a,
            .mkdf-pli-excerpt,
            .mkdf-pli-rating {
                color: #fff;
            }
	
	        .mkdf-pli-category a:hover {
		        color: rgba(#fff, .8);
	        }

	        .mkdf-pli-add-to-cart {
				opacity: 0;
				transform: translateY(10px);
				transition:  .3s .15s;
				pointer-events: all;

		        &.mkdf-default-skin {

			        .button,
			        .added_to_cart {
				        @include mkdfButtonDefaultStyle();
				        @include mkdfButtonSize();
				        color: $default-heading-color;
				        cursor: pointer;
				        z-index: 3;
				        padding: 0;
				
				        &:after {
					        content: '';
					        position: absolute;
					        right: 0;
					        bottom: 2px;
					        width: calc(100% + 4px);
					        height: 10px;
					        z-index: -1;
					        background-image: url('../img/simple_button_pattern.png');
            				background-repeat: repeat;
            				opacity: .7;
							transition: .5s;
				        }
				
				        &:hover:after {
							background-position: 20px 0;
				        }
			        }
		        }

		        &.mkdf-light-skin {

			        .button,
			        .added_to_cart {
				        @include mkdfButtonDefaultStyle();
				        @include mkdfButtonSize();
				        color: $default-heading-color;
				        cursor: pointer;
				        z-index: 3;
				        padding: 0;
				        
				        &:after {
					        content: '';
					        position: absolute;
					        right: 0;
					        bottom: 2px;
					        width: calc(100% + 4px);
					        height: 10px;
					        z-index: -1;
					        background-image: url('../img/simple_button_pattern.png');
            				background-repeat: repeat;
            				opacity: .7;
							transition: .5s;
				        }
				
				        &:hover:after {
					        background-position: 20px 0;
				        }
			        }
		        }
		
		        &.mkdf-dark-skin {
			
			        .button,
			        .added_to_cart {
				        @include mkdfButtonDefaultStyle();
				        @include mkdfButtonSize();
				        color: $default-background-color;
				        cursor: pointer;
				        z-index: 3;
				        padding: 0;
				
				        &:after {
					        content: '';
					        position: absolute;
					        right: 0;
					        bottom: 2px;
					        width: calc(100% + 4px);
					        height: 10px;
					        z-index: -1;
					        background-image: url('../img/simple_button_pattern.png');
            				background-repeat: repeat;
            				opacity: .7;
							transition: .5s;
				        }
				
				        &:hover:after {
					        background-position: 20px 0;
				        }
			        }
		        }
	        }

            .button,
            .added_to_cart {
	            @include mkdfButtonDefaultStyle();
	            @include mkdfButtonSize();
	            white-space: nowrap;
	            z-index: 3;
            }

            .button.added {
                display: none !important;
            }
        }
    }

    .mkdf-pli-text-wrapper {
        @include mkdfRelativeHolderLayout();
        margin: 21px 0 0;
	}
	
	.mkdf-pli-link {
		@include mkdfAbsoluteHolderLayout();
	}
	
	&.mkdf-product-info-light {
		
		.mkdf-pli-inner {
			
			.mkdf-pli-text-inner {
				
				.mkdf-pli-title a,
				.mkdf-pli-category a,
				.mkdf-pli-excerpt,
				.mkdf-pli-rating,
				.mkdf-pli-price {
					color: #fff;
				}
				
				.mkdf-pli-category a:hover {
					color: rgba(#fff, .8);
				}
			}
		}
	}
	
	&.mkdf-product-info-dark {
		
		.mkdf-pli-inner {

			.mkdf-pli-text {
				background-color: rgba($default-heading-color, 0.8);
			}

			.mkdf-pli-text-inner {
				
				.mkdf-pli-title a,
				.mkdf-pli-category a,
				.mkdf-pli-excerpt,
				.mkdf-pli-rating,
				.mkdf-pli-price {
					color: $default-background-color;
				}
				
				.mkdf-pli-category a:hover {
					color: rgba($default-background-color, .8);
				}
			}
		}
	}
	
	/*************** Masonry Layout - begin ***************/
	
	&.mkdf-masonry-layout {
		
		.mkdf-pli-inner {
			width: 100%;
			height: 100%;
			
			.mkdf-pli-image {
				height: 100%;
			}
		}
	}
	
	/*************** Masonry Layout - end ***************/
}
/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */