/* ==========================================================================
   WooCommerce Message styles - begin
   ========================================================================== */
.mkdf-woocommerce-page {

    .woocommerce-message,
    .woocommerce-info,
    .woocommerce-error {
        position: relative;
        display: block;
        padding: 20px 30px;
        margin: 0 0 30px;
        line-height: 38px;
        list-style: none;
        background-color: transparent;
        border: 1px solid #444444;

        > a {
            color: $default-text-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        a.wc-forward:not(.added_to_cart):not(.checkout-button) {
            top: 8px;
            float: right;
        }
    }

    .woocommerce-info {

        .showcoupon {
            color: $default-text-color;

            &:hover {
                color: $default-heading-color;
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Message styles - end
   ========================================================================== */