/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.widget.woocommerce {
	$thumb: 80px;
	$inner-padding: 15px; // distance between image and content
	$widget-list-item-padding: 15px; // same as sidebar vars
	
	clear: both;
	
	li {
		position: relative;
	}
	
	////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
	// title
	
	.product-title {
		@include mkdfHeadingStyle(h5);
		color: $default-heading-color !important;
		transition: color 0.2s ease-in-out;
		
		&:hover {
			//color: $default-text-color !important;
		}
	}
	
	////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
	// star rating
	
	li:not(.wc-layered-nav-rating) {
		.star-rating {
			display: none;
			margin-bottom: 7px;
			margin-top: 7px;
			
			span {
				max-width: 100px;
			}
		}
	}
	
	li.wc-layered-nav-rating {
		a {
			text-decoration: none;
		}
	}
	
	////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
	// price
	
	.amount {
		color: inherit;
		
		span { // currency symbol
			color: inherit;
		}
	}
	
	del,
	ins {
		display: block;
		
		// currency symbol
		span {
			color: inherit;
		}
	}
	
	ins {
		text-decoration: none;
	}
	
	////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
	// layered nav
	
	&.widget_layered_nav {
		.chosen {
			a {
				color: $first-main-color;
			}
		}
	}
	
	////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
	// widgets with images
	
	&.widget_shopping_cart,
	&.widget_products,
	&.widget_recently_viewed_products,
	&.widget_recent_reviews,
	&.widget_top_rated_products {
		ul {
			li {
				min-height: $thumb;
				position: relative;
				
				a {
					img {
						left: 0;
						max-width: $thumb;
						position: absolute;
						top: $widget-list-item-padding;
						width: 100%;
					}
					
					span {
						box-sizing: border-box;
						display: block;
						padding: 0 0 0 #{$thumb + $inner-padding};
						position: relative;
					}
				}
				
				> *:not(a) {
					clear: both;
					float: left;
					margin: 0 0 0 #{$thumb + $inner-padding};
				}
			}
		}
	}
	
	////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
	// widgets with images and plain text next to image
	
	&.widget_shopping_cart {
		a {
			display: block;
			padding: 0 0 0 #{$thumb + $inner-padding};
			
			@include mkdfH5();
		}
	}
	
	////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
	// recent reviews
	
	//&.widget_recent_reviews {
	//    .reviewer {
	//    }
	//}
	
	////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
	// shopping cart
	
	&.widget_shopping_cart {
		.remove {
			font-size: 0;
			font-weight: normal;
			line-height: 1;
			position: absolute;
			right: 0;
			top: 5px;
			text-decoration: none;
			
			&:after {
				content: "\4d";
				display: block;
				font-family: 'ElegantIcons';
				font-size: 15px;
				line-height: 15px;
				position: relative;
			}
		}
		
		.variation {
			@include mkdfRelativeHolderLayout();
			
			> * {
				display: inline-block;
				margin: 0;
				vertical-align: middle;
				
				p {
					margin: 0;
				}
			}
		}
		
		.total {
			margin: 17px 0 13px;
		}
		
		.buttons {
			.button {
				@include mkdfButtonDefaultStyle();
				@include mkdfButtonSize();
				@include mkdfButtonSolidColor();
				
				&:hover {
					@include mkdfButtonSolidHoverColor();
				}
				
				&:first-child {
					float: left;
				}
				
				&:last-child {
					float: right;
				}
			}
		}
	}
}

/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */