/* Woocommerce Product Page shortcode style - begin
========================================================================== */

div.woocommerce {

    > .single-product {

        .images {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            margin: 0;
            padding: 0;
    
            figure {
                position: relative;
                display: inline-block;
                vertical-align: top;
                margin: 0 -10px;
        
                a {
                    @include mkdfRelativeHolderLayout();
            
                    img {
                        position: relative;
                        width: 100%;
                        display: block;
                    }
                }
            }
    
            .woocommerce-product-gallery__trigger {
                position: absolute;
                top: 10px;
                right: 10px;
                width: 30px;
                height: 30px;
                line-height: 30px;
                text-align: center;
                z-index: 10;
            }
    
            .woocommerce-product-gallery__image {
                position: relative;
                display: inline-block;
                vertical-align: top;
                margin: 0;
                padding: 0 10px;
                box-sizing: border-box;
        
                &:first-child {
                    left: 10px;
                    width: calc(100% - 20px);
                    padding: 0;
                }
        
                .zoomImg {
                    cursor: crosshair;
                }
            }
    
            &.woocommerce-product-gallery--with-images {
        
                .woocommerce-product-gallery__image {
            
                    &:not(:first-child) {
                        float: left;
                        padding-top: 20px;
                    }
                }
            }
    
            $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
            @for $i from 0 to length($thumb_3_columns_label) {
                &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
                    $column_width: 100%/($i+2);
            
                    .woocommerce-product-gallery__image {
                
                        &:not(:first-child) {
                            width: $column_width;
                        }
                    }
                }
            }
        }
    
        .woocommerce-tabs {
            @include mkdfRelativeHolderLayout();
            margin: 115px 0 0;

            ul.tabs {
                @include mkdfRelativeHolderLayout();
                display: flex;
                align-items: flex-start;
                justify-content: center;
                list-style: none;
                margin: 0;
                padding: 0;
                text-align: center;
                clear: both;

                > li {
                    position: relative;
                    display: inline-block;
                    vertical-align: middle;

                    a {
                        position: relative;
                        display: inline-block;
                        vertical-align: top;
                        padding: 14px 109px;
                        font-family: $additional-button-font;
                        font-size: 12px;
                        letter-spacing: 0.250em;
                        text-transform: uppercase;
                        color: $default-heading-color;
                        border-bottom: 1px solid rgba(114, 114, 114, 0.6);
                        transition: border-color .3s;

                        &:hover {
                            border-bottom-color: $default-heading-color;
                        }
                    }

                    &.active {
                        a {
                            border-bottom-color: $default-heading-color;
                        }
                    }
                }
            }

            .entry-content {
                @include mkdfRelativeHolderLayout();
                margin: 0 auto;
                display: block;
                padding: 0 214px;
                box-sizing: border-box;

                > h2 {
                    display: none;
                }

                > p {
                    margin: 32px 0 20px;
                    text-align: center;
                }

                &.woocommerce-Tabs-panel--additional_information {

                    table th {
                        font-weight: 300;
                        color: #fff;
                    }
                }
            }

            table {
                margin: 32px 0 20px;

                tr, th {
                    border: 0;
                }

                td, th {
                    padding: 5px 0;
                    vertical-align: middle;
                    text-align: center;
                    width: 100%;
                    display: block;
                    margin: 0 auto;

                    p {
                        margin: 0;
                    }
                }

                th {
                    color: $default-heading-color;
                }
            }

            #reviews {
                @include mkdfRelativeHolderLayout();
                margin: 32px 0 20px;

                h2 {
                    display: none;
                }

                ol.commentlist {
                    list-style: none;

                    li {
                        margin: 0 0 25px;

                        &:last-child {
                            margin: 0;
                        }
                    }

                    .comment_container {
                        @include mkdfRelativeHolderLayout();

                        img {
                            float: left;
                            width: 74px;
                        }
                    }

                    .comment-text {
                        padding: 0 0 0 106px;

                        p {
                            margin: 0;
                        }

                        .star-rating {
                            top: 4px;
                            float: right;
                        }

                        .meta {
                            padding-bottom: 13px;

                            .woocommerce-review__author {
                                @include mkdfHeadingStyle(h5);
                                padding-right: 8px;
                            }

                            .woocommerce-review__dash {
                                display: none;
                            }

                            .woocommerce-review__published-date {
                                @include mkdfHeadingStyle(h6);
                            }
                        }
                    }
                }

                .comment-respond {
                    margin-top: 26px;

                    .comment-reply-title {
                        @include mkdfHeadingStyle(h5);
                        margin: 0 0 18px;
                    }

                    p {
                        margin: 0;
                    }

                    .comment-form-rating {
                        padding-top: 8px;
                    }

                    .comment-form-comment {
                        padding-top: 8px;

                        label {
                            //display: none;
                        }
                    }

                    .form-submit {
                        position: absolute;
                        display: block;
                        padding-top: 20px;

                        .submit {
                            padding: 0!important;
                            color: #fff;
                            background-color: transparent;
                            border: 0;
                            vertical-align: middle;

                        }

                        &:after {
                            content: '';
                            position: absolute;
                            right: 0;
                            bottom: 2px;
                            width: calc(100% + 4px);
                            height: 10px;
                            z-index: 1;
                            background-image: url('../img/simple_button_pattern.png');
                            background-repeat: repeat;
                            opacity: .7;
                            transition: .5s;
                        }

                        &:hover:after {
                            background-position: 20px 0;
                        }
                    }

                    .stars {
                        overflow: hidden;

                        > span {
                            display: flex;
                        }

                        a {
                            position: relative;
                            display: inline-block;
                            color: transparent;
                            font-size: 0;
                            margin: 0;
                            @include mkdfWooCommerceSingleProductRatingsStyle();
                        }
                    }
                }
            }
        }

        .related.products,
        .upsells.products {
            margin: 154px 0 0;

            > h2 {
                display: block;
                margin: 36px 0 40px;
                @include mkdfHeadingStyle(h4);
            }

            .product {

                .mkdf-pl-text-wrapper {
                    margin-bottom: 0;
                }
            }
        }
    }
}

/* Woocommerce Product Page shortcode style - end
========================================================================== */