/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - begin
   ========================================================================== */

@mixin mkdfWooCommerceMarksGeneralStyle() {
	position: absolute;
	display: block;
	top: 0;
	width: 73px;
	height: 40px;
	margin: 0;
	padding: 6px 0;
	@include mkdfSubTitle();
	font-weight: 400;
	color: #d8d8d8;
	text-align: center;
	z-index: 100;
	box-sizing: border-box;
	transform: translateZ(0);
}

@mixin mkdfWooCommerceSaleStyle() {
	right: 0;
	background-color: rgba($default-background-color, .8);
}

@mixin mkdfWooCommerceOutOfStockStyle() {
	right: 0;
	background-color: rgba($default-background-color, .8);
}

@mixin mkdfWooCommerceNewProductStyle() {
	right: 0;
	background-color: rgba($default-background-color, .8);
}

/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Price mixin styles - begin
   ========================================================================== */

@mixin mkdfWooCommercePriceStyle() {
	position: relative;
	display: block;
	margin: 0;
	line-height: 1;
	@include mkdfSubTitle();
	color: #d8d8d8;
	
	* {
		font-size: inherit;
		line-height: inherit;
	}
	
	del {
		margin: 0 12px 0 0;
		color: $default-text-color;
		text-decoration: none;
		
		> span {
			position: relative;
			
			&:before {
				position: absolute;
				content: '';
				width: calc(100% + 7px);
				height: 1px;
				background-color: $default-text-color;
				top: calc(50% + 3px);
				left: 0;
			}
		}
	}
	
	ins {
		text-decoration: none;
	}
}

/* ==========================================================================
   Price mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Star Ratings mixin styles - begin
   ========================================================================== */

%star-ratings {
	font-family: "ElegantIcons";
	font-size: 12px;
	line-height: 1;
	letter-spacing: 2px;
	color: $default-text-color;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

@mixin mkdfWooCommerceRatingsStyle() {
	position: relative;
	display: inline-block;
	vertical-align: middle;
	@extend %star-ratings;
	overflow: hidden;
	
	&:before {
		content: "\e031\e031\e031\e031\e031";
	}
	
	span {
		position: absolute;
		top: 0;
		left: 0;
		overflow: hidden;
		
		&:before {
			content: "\e033\e033\e033\e033\e033";
		}
	}
}

@mixin mkdfWooCommerceSingleProductRatingsStyle() {
	
	&:before {
		content: "\e031";
		position: relative;
		display: inline-block;
		vertical-align: middle;
		@extend %star-ratings;
		overflow: hidden;
		z-index: -1;
	}
	
	&.active:after {
		content: "\e033\e033\e033\e033\e033";
		position: absolute;
		right: 0;
		bottom: 6px;
		display: block;
		@extend %star-ratings;
	}
}

/* ==========================================================================
   Star Ratings mixin styles - end
   ========================================================================== */