/* ==========================================================================
   WooCommerce Quantity Inputs styles - begin
   ========================================================================== */

.woocommerce-page .mkdf-content,
div.woocommerce {

    .mkdf-quantity-buttons {
        position: relative;
        display: inline-block;
        vertical-align: middle;

        .mkdf-quantity-input,
        .mkdf-quantity-minus,
        .mkdf-quantity-plus {
            display: inline-block;
            vertical-align: middle;
            color: $default-heading-color;
            background-color: transparent;
            text-align: center;
            -webkit-user-select: none;
            box-sizing: border-box;
        }

        .mkdf-quantity-input {
            position: relative;
            height: 44px;
            width: 30px;
            padding: 0;
            background-image: none;
            margin: 0;
            font-size: 25px;
            font-family: $default-heading-font;
            line-height: 42px;
            outline: 0;
        }

        .mkdf-quantity-minus,
        .mkdf-quantity-plus {
            position: relative;
            right: 0;
            width: 22px;
            height: 22px;
            line-height: 20px;
            border-left: 0;
            cursor: pointer;
            transition: color .15s ease-in-out;

            &:hover {
                color: $first-main-color;
            }
        }

        .mkdf-quantity-plus {
            top: 0;
            border-bottom: 0;
        }

        .mkdf-quantity-minus {
            bottom: 0;
        }
    }
}

.woocommerce-page .mkdf-content,
div.woocommerce {

    input[type="text"],
    input[type="email"],
    input[type="tel"],
    input[type="password"],
    textarea {
        @extend %input-style;
    }

    .coupon {

        input[type="text"] {
            width: 200px;
        }
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs styles - end
   ========================================================================== */