/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.mkdf-woo-single-page {
	
	&.mkdf-woo-single-thumb-on-left-side {
		
		.mkdf-single-product-content {
			
			.images {
				
				@media only screen and (min-width: $ipad-portrait-plus-pixel) {
					
					figure {
						width: 100%;
						margin: 0;
						overflow: hidden;
					}
					
					$thumb_size: 100;
					$space_between_items: 20;
					
					.woocommerce-product-gallery__image {
						
						&:first-child {
							$first_thumb_offset: $thumb_size + $space_between_items;
							
							left: #{$first_thumb_offset}px;
							width: calc(100% - #{$first_thumb_offset}px);
						}
					}
					
					&.woocommerce-product-gallery--with-images {
						
						.woocommerce-product-gallery__image {
							
							&:not(:first-child) {
								position: absolute;
								top: 0;
								left: 0;
								width: #{$thumb_size}px;
								padding: 0 0 #{$space_between_items}px;
								box-sizing: border-box;
								
								a {
									height: 100%;
									
									img {
										height: 100%;
									}
								}
							}
						}
					}
					
					$thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
					@for $i from 0 to length($thumb_3_columns_label) {
						&.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
							$column_value: $i+2;
							$column_size: 100 / $column_value;
							$column_width: calc(#{100% / $column_value} + #{$space_between_items / $column_value}px);
							
							.woocommerce-product-gallery__image {
								
								&:not(:first-child) {
									height: $column_width;
								}
								
								$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
								@for $i from 0 to length($thumbnail_numbers) {
									$thumbnail_number_value: nth($thumbnail_numbers, $i+1);
									
									&:nth-child(#{$thumbnail_number_value}#{n}) {
										$first_param: ($column_size * $i)#{'%'};
										$second_param: ($space_between_items / $column_value * $i)#{px};
										$topPos: calc(#{$first_param} + #{$second_param});
										
										top: $topPos;
									}
								}
							}
						}
					}
				}
			}
		}
	}
	
	.product {
		@include mkdfRelativeHolderLayout();
		float: none;
		margin: 0 0 73px;
	}
	
	.mkdf-single-product-content {
		@include mkdfRelativeHolderLayout();
		clear: both;
		margin: 0 0 20px;
		
		.images {
			position: relative;
			display: inline-block;
			vertical-align: middle;
			width: 57%;
			margin: 0;
			padding: 0 24px 0 0;
			float: left;
			box-sizing: border-box;
			
			figure {
				position: relative;
				display: inline-block;
				vertical-align: top;
				margin: 0 -10px;
				
				a {
					@include mkdfRelativeHolderLayout();
					
					img {
						position: relative;
						width: 100%;
						display: block;
					}
				}
			}
			
			.woocommerce-product-gallery__trigger {
				position: absolute;
				top: 10px;
				right: 39px;
				width: 30px;
				height: 30px;
				line-height: 30px;
				text-align: center;
				z-index: 10;
			}
			
			.woocommerce-product-gallery__image {
				position: relative;
				display: inline-block;
				vertical-align: top;
				margin: 0;
				padding: 0 15px;
				box-sizing: border-box;
				
				&:first-child {
					left: 10px;
					width: calc(100% - 20px);
					padding: 0;
				}
				
				.zoomImg {
					cursor: crosshair;
				}
			}
			
			&.woocommerce-product-gallery--with-images {
				
				.woocommerce-product-gallery__image {
					
					&:not(:first-child) {
						float: left;
						padding-top: 31px;
					}
				}
			}
			
			$thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
			@for $i from 0 to length($thumb_3_columns_label) {
				&.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
					$column_width: 100%/($i+1);
					
					.woocommerce-product-gallery__image {
						
						&:not(:first-child) {
							width: $column_width;
						}
					}
				}
			}
		}
	}
	
	.mkdf-single-product-summary {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		width: 43%;
		padding: 0 0 0 24px;
		float: left;
		box-sizing: border-box;
		
		.mkdf-single-product-title {
			display: block;
			margin: 0;
			@include mkdfHeadingStyle(h3);
		}
		
		.price {
			@include mkdfWooCommercePriceStyle();
			font-size: 49px;
			
			.woocommerce-Price-currencySymbol {
				padding-right: 13px;
			}
		}
		
		.woocommerce-product-rating {
			@include mkdfRelativeHolderLayout();
			line-height: 1;
			margin: 18px 0 3px;
		}
		
		.woocommerce-product-details__short-description {
			@include mkdfRelativeHolderLayout();
			margin: 32px 0 0;
			
			p {
				margin: 0;
			}
		}
		
		.product_meta {
			margin: 36px 0 0;
			
			> span {
				display: block;
				margin: 3px 0;
				color: $default-heading-color;
				
				.sku {
					color: $default-text-color;
				}
				
				a {
					color: $default-text-color;
					padding-left: 6px;
					
					&:hover {
						color: $first-main-color;
					}
				}
			}
		}
		
		.mkdf-woo-social-share-holder {
			@include mkdfRelativeHolderLayout();
			margin: 11px 0 0;
			
			.mkdf-social-share-holder {
				display: table-cell;
				vertical-align: top;
			}
			
			.mkdf-social-title {
				margin: 0 7px 0 0;
				color: $default-heading-color;
			}

			ul li {
				margin-right: 24px;
			}
		}
		
		p.stock.out-of-stock,
		p.stock.in-stock {
			color: $default-heading-color;
			font-weight: bold;
			margin: 18px 0 0;
		}
		
		form.cart {
			@include mkdfRelativeHolderLayout();
			margin: 39px 0 0;
			
			.single_add_to_cart_button {
				@include mkdfButtonSolidColor();
				@include mkdfButtonSize();
				border: 0;
				
				&:hover {
					@include mkdfButtonSolidHoverColor();
				}
				
				&:after {
					content: none;
				}
			}
		}
		
		.mkdf-quantity-buttons {
			margin: 0 45px 0 0;
		}
	}
	
	.woocommerce-tabs {
		@include mkdfRelativeHolderLayout();
		margin: 115px 0 0;
		
		ul.tabs {
			@include mkdfRelativeHolderLayout();
			display: flex;
			align-items: flex-start;
			justify-content: center;
			list-style: none;
			margin: 0;
			padding: 0;
			text-align: center;
			clear: both;
			
			> li {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				
				a {
					position: relative;
					display: inline-block;
					vertical-align: top;
					padding: 14px 109px;
					font-family: $additional-button-font;
					font-size: 12px;
					letter-spacing: 0.250em;
					text-transform: uppercase;
					color: $default-heading-color;
					border-bottom: 1px solid rgba(114, 114, 114, 0.6);
					transition: border-color .3s;
					
					&:hover {
						border-bottom-color: $default-heading-color;
					}
				}
				
				&.active {
					a {
						border-bottom-color: $default-heading-color;
					}
				}
			}
		}
		
		.entry-content {
			@include mkdfRelativeHolderLayout();
			margin: 0 auto;
			display: block;
			padding: 0 214px;
			box-sizing: border-box;
			
			> h2 {
				display: none;
			}
			
			> p {
				margin: 32px 0 20px;
				text-align: center;
			}

			&.woocommerce-Tabs-panel--additional_information {

				table th {
					font-weight: 300;
					color: #fff;
				}
			}
		}
		
		table {
			margin: 32px 0 20px;
			
			tr, th {
				border: 0;
			}
			
			td, th {
				padding: 5px 0;
				vertical-align: middle;
				text-align: center;
				width: 100%;
				display: block;
				margin: 0 auto;
				
				p {
					margin: 0;
				}
			}
			
			th {
				color: $default-heading-color;
			}
		}
		
		#reviews {
			@include mkdfRelativeHolderLayout();
			margin: 32px 0 20px;
			
			h2 {
				display: none;
			}
			
			ol.commentlist {
				list-style: none;
				
				li {
					margin: 0 0 25px;
					
					&:last-child {
						margin: 0;
					}
				}
				
				.comment_container {
					@include mkdfRelativeHolderLayout();
					
					img {
						float: left;
						width: 74px;
					}
				}
				
				.comment-text {
					padding: 0 0 0 106px;
					
					p {
						margin: 0;
					}
					
					.star-rating {
						top: 4px;
						float: right;
					}
					
					.meta {
						padding-bottom: 13px;
						
						.woocommerce-review__author {
							@include mkdfHeadingStyle(h5);
							padding-right: 8px;
						}
						
						.woocommerce-review__dash {
							display: none;
						}
						
						.woocommerce-review__published-date {
							@include mkdfHeadingStyle(h6);
						}
					}
				}
			}
			
			.comment-respond {
				margin-top: 26px;
				
				.comment-reply-title {
					@include mkdfHeadingStyle(h5);
					margin: 0 0 18px;
				}
				
				p {
					margin: 0;
				}
				
				.comment-form-rating {
					padding-top: 8px;
				}
				
				.comment-form-comment {
					padding-top: 8px;
					
					label {
						//display: none;
					}
				}
				
				.form-submit {
					position: absolute;
					display: block;
					padding-top: 20px;
					
					.submit {
						padding: 0!important;
						color: #fff;
						background-color: transparent;
						border: 0;
						vertical-align: middle;
						
					}
					
					&:after {
						content: '';
						position: absolute;
						right: 0;
						bottom: 2px;
						width: calc(100% + 4px);
						height: 10px;
						z-index: 1;
						background-image: url('../img/simple_button_pattern.png');
            			background-repeat: repeat;
            			opacity: .7;
            			transition: .5s;
					}
					
					&:hover:after {
						background-position: 20px 0;
					}
				}
				
				.stars {
					overflow: hidden;

					> span {
						display: flex;
					}

					a {
						position: relative;
						display: inline-block;
						color: transparent;
						font-size: 0;
						margin: 0;
						@include mkdfWooCommerceSingleProductRatingsStyle();
					}
				}
			}
		}
	}
	
	.related.products,
	.upsells.products {
		margin: 154px 0 0;
		
		> h2 {
			display: block;
			margin: 36px 0 40px;
			@include mkdfHeadingStyle(h4);
		}
		
		.product {
			
			.mkdf-pl-text-wrapper {
				margin-bottom: 0;
			}
		}
	}
}

/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */