/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.mkdf-shopping-cart-holder {
	position: relative;
	display: inline-block;
	vertical-align: top;
	height: 100%;
	margin: 0 10px;
	padding: 0 10px;
	
	.touch & {
		cursor: pointer;
	}
	
	&:hover {
		
		.mkdf-sc-dropdown {
			max-height: 100vh;
            pointer-events: auto;
            transition: .5s cubic-bezier(0.66, 0.14, 0.58, 1) .1s;
		}
	}
	
	.mkdf-shopping-cart-inner {
		@include mkdfRelativeHolderLayout();
		height: 100%;
	}
	
	.mkdf-header-cart {
		position: relative;
		display: flex;
		align-items: center;
		height: 100%;
		
		.mkdf-sc-opener-icon {
			position: relative;
			display: inline-block;
			vertical-align: top;
			line-height: 1;
		}
		
		&.mkdf-header-cart-icon-pack {
			
			.mkdf-sc-opener-icon {
				color: $default-heading-color;
				font-size: 18px;
				
				* {
					display: block;
					line-height: inherit;
					
					&:before {
						display: block;
						line-height: inherit;
					}
				}
			}
			
			.mkdf-sc-opener-count {
				position: absolute;
				display: inline-block;
				width: 9px;
				height: 9px;
				line-height: 9px;
				float: right;
				top: 39px;
				text-align: center;
				right: -4px;
				font-size: 8px;
				font-family: $additional-button-font;
				font-weight: 700;
				background-color: $default-heading-color;
				color: $default-background-color;
				border-radius: 50%;
			}
		}
		
		&.mkdf-header-cart-svg-path {
			
			.mkdf-sc-opener-icon {
				
				> * {
					vertical-align: middle;
				}
				
				> *,
				path {
					fill: currentColor;
				}
			}
		}
	}
}

.mkdf-dark-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.fixed) .mkdf-shopping-cart-holder .mkdf-header-cart {
	color: $header-dark-color;
	
	&:hover {
		color: $header-dark-hover-color;
	}
	
	&.mkdf-header-cart-icon-pack {
		
		.mkdf-sc-opener-icon {
			color: $default-background-color;
		}
		
		.mkdf-sc-opener-count {
			background-color: $default-background-color;
			color: $default-heading-color;
		}
	}
}

.mkdf-light-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.fixed) .mkdf-shopping-cart-holder .mkdf-header-cart {
	color: $header-light-color;
	
	&:hover {
		color: $header-light-hover-color;
	}
}

.mkdf-sc-dropdown {
	position: absolute;
	top: 100%;
	left: auto;
	right: 0;
	text-align: left;
	box-sizing: border-box;
    max-height: 0;
    pointer-events: none;
    overflow: hidden;
    will-change: transform;
    transition: .2s;
	
	.mkdf-sc-dropdown-inner {
		@include mkdfRelativeHolderLayout();
		width: 326px;
		padding: 28px;
		background-color: $default-background-color;
		box-sizing: border-box;
	}
	
	.mkdf-sc-dropdown-items {
		@include mkdfRelativeHolderLayout();
	}
	
	.mkdf-sc-dropdown-item {
		position: relative;
		display: flex;
		align-items: flex-start;
		margin-top: 20px;
		
		&:first-child {
			margin-top: 0;
		}
	}
	
	.mkdf-sc-dropdown-item-image {
		flex-shrink: 0;
		width: 104px;
		margin-right: 20px;
		
		a, img {
			display: block;
		}
	}
	
	.mkdf-sc-dropdown-item-content {
		@include mkdfRelativeHolderLayout();
		padding-right: 20px;
	}
	
	.mkdf-sc-dropdown-item-title {
		margin: 0;
	}
	
	.mkdf-sc-dropdown-item-price {
		margin: 7px 0 0;
		@include mkdfSubTitle();
	}
	
	.mkdf-sc-dropdown-item-quantity {
		margin: 7px 0 0;
		@include mkdfSubTitle();
	}
	
	.mkdf-sc-dropdown-item-remove {
		position: absolute;
		top: -2px;
		right: 0;
		font-size: 20px;
		line-height: 1;
	}
	
	.mkdf-sc-dropdown-subtotal {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-top: 20px;
		
		> * {
			margin: 0;
			font-family: $default-text-font;
			font-size: 15px;
			color: $default-text-color;
			text-transform: capitalize;
			padding-top: 3px;
			font-weight: 300;
			font-style: normal;
		}
	}
	
	.mkdf-sc-dropdown-button-holder {
		@include mkdfRelativeHolderLayout();
		margin-top: 12px;
	}
	
	.mkdf-sc-dropdown-button {
		@include mkdfButtonDefaultStyle();
		@include mkdfButtonSize(huge);
		@include mkdfButtonSolidColor();
		
		&:hover {
			@include mkdfButtonSolidHoverColor();
		}
	}
}

.mkdf-vertical-menu-area {
	
	.mkdf-sc-dropdown {
		display: none !important;
	}
}

/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */