/* ==========================================================================
   Product List - Carousel shortcode styles - begin
   ========================================================================== */

.mkdf-plc-holder {
    @include mkdfRelativeHolderLayout();

    .mkdf-plc-outer {
        @include mkdfRelativeHolderLayout();
    }

    .mkdf-plc-item {
        position: relative;
        display: inline-block;
        vertical-align: top;
        width: 100%;
        padding: 0;
        z-index: 100;
        overflow: hidden;
        box-sizing: border-box;

        &:hover {

            .mkdf-plc-image-outer {

                .mkdf-plc-text {
                    opacity: 1;
                }
            }
        }

        .touch & {
            cursor: pointer;
        }

        .mkdf-plc-image-outer {
            @include mkdfRelativeHolderLayout();

            .mkdf-plc-image {
                @include mkdfRelativeHolderLayout();

                .mkdf-plc-onsale,
                .mkdf-plc-out-of-stock,
                .mkdf-plc-new-product {
	                @include mkdfWooCommerceMarksGeneralStyle();
                }

                .mkdf-plc-onsale {
	                @include mkdfWooCommerceSaleStyle();
                }

                .mkdf-plc-out-of-stock {
	                @include mkdfWooCommerceOutOfStockStyle();
                }

	            .mkdf-plc-new-product {
		            @include mkdfWooCommerceNewProductStyle();
	            }
            }

            .mkdf-plc-text {
                position: absolute;
                top: 0;
                left: 0;
                display: inline-block;
                width: 100%;
                height: 100%;
                padding: 10px;
                opacity: 0;
                background-color: rgba($default-background-color, 0.6);
                box-sizing: border-box;
                transition: opacity .2s ease-out, background-color .2s ease-out;
            }

            .mkdf-plc-text-outer {
                position: relative;
                display: table;
                width: 100%;
                height: 100%;
                text-align: center;
            }

            .mkdf-plc-text-inner {
                position: relative;
                display: table-cell;
                width: 100%;
                height: 100%;
                vertical-align: middle;
            }
        }

        .mkdf-plc-title {
		    margin: 0 0 4px;
	    }

	    .mkdf-plc-category {
		    margin: 11px 0 3px;
	    }

        .mkdf-plc-excerpt {
            color: $default-heading-color;
	        line-height: 1.3em;
            margin: 11px 0 3px;
        }

	    .mkdf-plc-rating-holder {
		    @include mkdfRelativeHolderLayout();
		    line-height: 1;
		    margin: 10px 0 3px;
	    }

	    .mkdf-plc-rating {
		    @include mkdfWooCommerceRatingsStyle();
	    }

        .mkdf-plc-price {
	        @include mkdfWooCommercePriceStyle();
	        padding-bottom: 12px;
        }

	    .button,
	    .added_to_cart {
		    @include mkdfButtonDefaultStyle();
		    @include mkdfButtonSize();
		    white-space: nowrap;
		    z-index: 3;
	    }

	    .button.added {
		    display: none;
	    }

	    .mkdf-plc-add-to-cart {
		    margin: 15px 0 0;

	        &.mkdf-default-skin {

		        .button,
		        .added_to_cart {
			        @include mkdfButtonDefaultStyle();
			        @include mkdfButtonSize();
			        color: $default-heading-color;
			        cursor: pointer;
			        z-index: 3;
			        padding: 0;

			        &:after {
				        content: '';
				        position: absolute;
				        right: 0;
				        bottom: 2px;
				        width: calc(100% + 4px);
				        height: 10px;
				        z-index: -1;
						background-image: url('../img/simple_button_pattern.png');
						background-repeat: repeat;
						opacity: .7;
            			transition: .5s;
			        }

			        &:hover:after {
						opacity: 0;
						background-position: 20px 0;
			        }
		        }
	        }

	        &.mkdf-light-skin {

		        .button,
		        .added_to_cart {
			        @include mkdfButtonDefaultStyle();
			        @include mkdfButtonSize();
			        color: $default-heading-color;
			        cursor: pointer;
			        z-index: 3;
			        padding: 0;

			        &:after {
				        content: '';
				        position: absolute;
				        right: 0;
				        bottom: 2px;
				        width: calc(100% + 4px);
				        height: 10px;
				        z-index: -1;
				        background-image: url('../img/simple_button_pattern.png');
            			background-repeat: repeat;
            			opacity: .7;
            			transition: .5s;
			        }

			        &:hover:after {
				        background-position: 20px 0;
			        }
		        }
	        }

	        &.mkdf-dark-skin {

		        .button,
		        .added_to_cart {
			        @include mkdfButtonDefaultStyle();
			        @include mkdfButtonSize();
			        color: $default-background-color;
			        cursor: pointer;
			        z-index: 3;
			        padding: 0;

			        &:after {
				        content: '';
				        position: absolute;
				        right: 0;
				        bottom: 2px;
				        width: calc(100% + 4px);
				        height: 10px;
				        z-index: -1;
				        background-image: url('../img/simple_button_pattern.png');
            			background-repeat: repeat;
            			opacity: .7;
            			transition: .5s;
			        }

			        &:hover:after {
				        background-position: 20px 0;
			        }
		        }
	        }
        }

        .mkdf-plc-link {
            @include mkdfAbsoluteHolderLayout();
        }
    }

    &.mkdf-standard-layout {

        .mkdf-plc-item {

            .mkdf-plc-title a,
            .mkdf-plc-category a,
            .mkdf-plc-excerpt,
            .mkdf-plc-rating,
            .mkdf-plc-price {
                color: #fff;
            }

	        .mkdf-plc-category a:hover {
		        color: rgba(#fff, .8);
	        }
        }
    }

    &.mkdf-simple-layout {

        .mkdf-plc-image-outer {
            display: table-cell;
            vertical-align: middle;
            width: 50%;
            padding: 0 15px 0 0;
        }

        .mkdf-plc-text-wrapper {
            position: relative;
            display: table-cell;
            vertical-align: middle;
            width: 50%;
            padding: 0 0 0 15px;
        }

        .mkdf-plc-text,
        .mkdf-plc-text-outer,
        .mkdf-plc-text-inner {
            @include mkdfRelativeHolderLayout();
            -webkit-transform: translateZ(0);
        }
    }

	&.mkdf-product-info-light {

		.mkdf-plc-item {

			.mkdf-plc-text-inner {

				.mkdf-plc-title a,
				.mkdf-plc-category a,
				.mkdf-plc-excerpt,
				.mkdf-plc-rating,
				.mkdf-plc-price {
					color: #fff;
				}

				.mkdf-plc-category a:hover {
					color: rgba(#fff, .8);
				}
			}
		}
	}

	&.mkdf-product-info-dark {

		.mkdf-plc-item {

			.mkdf-plc-text {
				background-color: rgba($default-heading-color, 0.8);
			}

			.mkdf-plc-text-inner {

				.mkdf-plc-title a,
				.mkdf-plc-category a,
				.mkdf-plc-excerpt,
				.mkdf-plc-rating,
				.mkdf-plc-price {
					color: $default-background-color;
				}

				.mkdf-plc-category a:hover {
					color: rgba($default-background-color, .8);
				}
			}
		}
	}

	&.mkdf-plc-nav-light-skin {
		
		.owl-nav {
			
			.owl-prev,
			.owl-next {
				color: $header-light-color;
				
				&:hover {
					color: $header-light-hover-color;
				}
			}
		}
	}
	
	&.mkdf-plc-pag-inside-slider {
		
		.owl-prev,
		.owl-next {
			transform: translateY(-50%);
		}
		
		.owl-dots {
			position: absolute;
			left: 0;
			bottom: 10px;
			margin: 0;
		}
	}
	
	&.mkdf-plc-pag-light-skin {
		
		.owl-dots {
			
			.owl-dot {

				span {
					background-color: $header-light-hover-color;
				}

				&:hover,
				&.active {

					span {
						background-color: $header-light-color;
					}
				}
			}
		}
	}
}
/* ==========================================================================
   Product List - Carousel shortcode styles - end
   ========================================================================== */