/* ==========================================================================
   WooCommerce Buttons styles - begin
   ========================================================================== */

.woocommerce-page .mkdf-content,
div.woocommerce {
	
	a.button,
	a.added_to_cart,
	input[type="submit"],
	button[type="submit"]:not(.mkdf-search-submit),
	.wc-forward:not(.added_to_cart):not(.checkout-button) {
		@include mkdfButtonDefaultStyle();
		@include mkdfButtonSize();
		color: $default-heading-color;
		cursor: pointer;
		z-index: 3;
		padding: 0;
		
		background-color: transparent;
		border: 0;
		
		&:after {
			content: '';
			position: absolute;
			right: 0;
			bottom: 2px;
			width: calc(100% + 4px);
			height: 10px;
			z-index: -1;
			background-image: url('../img/simple_button_pattern.png');
            background-repeat: repeat;
            opacity: .7;
            transition: .5s;
		}
		
		&:hover:after {
			background-position: 20px 0;
		}
	}
	
	a.button.added {
		display: none;
	}
}

/* ==========================================================================
   WooCommerce Buttons styles - end
   ========================================================================== */