/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.widget.woocommerce {
	clear: both;
	
	&.widget_rating_filter,
	&.widget_shopping_cart,
	&.widget_layered_nav,
	&.widget_layered_nav_filters,
	&.widget_product_categories,
	&.widget_products,
	&.widget_recently_viewed_products,
	&.widget_recent_reviews,
	&.widget_top_rated_products {
		
		ul {
			padding: 0;
			margin: 0;
			list-style: none;
			
			li {
				@include mkdfRelativeHolderLayout();
				padding: 0;
				margin: 0 0 15px;
				clear: both;
				
				&:last-child {
					margin: 0;
				}
				
				ul {
					margin: 14px 0 0 10px;
				}
			}
		}
	}
	
	&.widget_shopping_cart {
		
		.widget_shopping_cart_content {
			@include mkdfRelativeHolderLayout();
			
			ul {
				
				li {
					min-height: 110px;
					
					a {
						@include mkdfHeadingStyle(h5);
						padding: 0 0 10px 125px;
						
						img {
							position: absolute;
							top: 0;
							left: 0;
							width: 100%;
							max-width: 104px;
						}
					}
					
					.quantity {
						position: relative;
						display: block;
						padding: 0 0 0 125px;
						margin: 0;
						line-height: 20px;
						box-sizing: border-box;
						@include mkdfSubTitle();
						
					}
					
					a.remove {
						position: absolute;
						top: 0;
						right: 0;
						font-size: 0;
						line-height: 1;
						
						&:after {
							position: relative;
							display: block;
							padding: 0 3px;
							font-family: 'Ionicons';
							content: "\f404";
							font-size: 30px;
							line-height: 25px;
						}
					}
				}
			}
			
			.variation {
				@include mkdfRelativeHolderLayout();
				
				> * {
					display: inline-block;
					vertical-align: middle;
					margin: 0;
					
					p {
						margin: 0;
					}
				}
			}
			
			.total {
				margin: 17px 0 13px;
			}
			
			.woocommerce-mini-cart__buttons {
				margin: 10px 0 0;
				
				.button {
					padding: 0;
					background-color: transparent;
					
					&:hover {
						background-color: transparent;
					}
					
					&:last-child {
						float: left;
						margin-left: 40px;
					}
				}
			}
		}
	}
	
	&.widget_layered_nav {
		
		.mkdf-widget-title {
			margin: 0 0 17px;
		}
		
		ul {
			
			li {
				margin: 2px 0;
				
				&.chosen {
					
					a {
						color: $first-main-color;
					}
				}
			}
		}
	}
	
	&.widget_layered_nav_filters {
		
		.mkdf-widget-title {
			margin: 0 0 17px;
		}
	}
	
	&.widget_price_filter {
		
		.price_slider_wrapper {
			
			.ui-widget-content {
				position: relative;
				height: 3px;
				background-color: #e5e5e5;
				
				.ui-slider-handle {
					position: absolute;
					top: -4px;
					height: 10px;
					width: 3px;
					background-color: $default-heading-color;
					margin: 0 0 0 -1px;
					cursor: pointer;
					outline: medium none;
					z-index: 2;
					
					&:last-child {
						transform: translateX(-50%);
					}
				}
				
				.ui-slider-range {
					position: absolute;
					display: block;
					height: 100%;
					background-color: $default-heading-color;
					z-index: 1;
				}
			}
		}
		
		.price_slider_amount {
			margin: 33px 0 0;
			
			.button {
				float: right;
			}
			
			.price_label {
				float: left;
			}
		}
	}
	
	&.widget_product_categories {
		
		ul {
			
			li {
				margin: 2px 0;

				a {
					font-family: $additional-text-font;
					font-size: 19px;
					font-weight: 400;
					font-style: italic;
				}
				
				ul {
					margin: 0 0 0 10px;
				}
			}
		}
	}
	
	&.widget_products,
	&.widget_recently_viewed_products,
	&.widget_recent_reviews,
	&.widget_top_rated_products {
		
		.mkdf-widget-title {
			margin: 0 0 29px;
		}
		
		ul {
			
			li {
				min-height: 110px;
				
				a {
					
					img {
						position: absolute;
						top: 0;
						left: 0;
						width: 100%;
						max-width: 104px;
					}
					
					span {
						position: relative;
						display: block;
						padding: 0 0 0 125px;
						line-height: 20px;
						box-sizing: border-box;
					}
				}
				
				> *:not(a) {
					margin: 0 0 0 125px;
					line-height: 20px;
				}
				
				.star-rating {
					position: relative;
					
					span {
						max-width: 100px;
					}
				}
				
				.woocommerce-Price-amount {
					@include mkdfSubTitle();
					padding-top: 7px;
				}
				
				del,
				ins {
					display: block;
					
					span {
						color: inherit;
					}
				}
				
				ins {
					position: absolute;
					display: inline-block;
					margin-left: 12px;
					text-decoration: none;
				}
			}
		}
	}
	
	&.widget_recent_reviews {
		
		ul {
			
			li {
				
				a {
					span {
						padding: 0 0 0 23px;
					}
				}
			}
		}
	}
	
	&.widget_products,
	&.widget_recently_viewed_products,
	&.widget_top_rated_products {
		
		ul {
			
			li {
				
				.product-title {
					@include mkdfHeadingStyle(h5);
					color: $default-heading-color;
				}
				
				.amount {
					color: $default-heading-color;
				}
			}
		}
	}
	
	&.widget_recently_viewed_products {
		
		ul {
			
			li {
				
				del,
				> .amount {
					display: block;
					margin-top: 10px;
				}
			}
		}
	}
	
	&.widget_recent_reviews {
		
		ul {
			
			li {
				
				.star-rating {
					margin-top: 7px;
					margin-bottom: 10px;
				}
			}
		}
		
		a {
			display: block;
			padding: 0 0 0 102px;
		}
		
		.reviewer {
			display: inline-block;
			vertical-align: middle;
			width: auto;
			clear: both;
		}
	}
	
	&.widget_top_rated_products {
		
		ul {
			
			li {
				
				.star-rating {
					margin-top: 10px;
					margin-bottom: 12px;
				}
				
				.amount {
					display: block;
				}
			}
		}
	}
	
	&.widget_product_tag_cloud {
		
		.tagcloud {
			
			a {
				position: relative;
				display: inline-block;
				vertical-align: top;
				margin: 0 24px 6px 0;
				font-family: inherit;
				font-size: 15px !important;
				line-height: inherit;
				font-weight: inherit;
				letter-spacing: inherit;
				text-transform: capitalize;
				color: $default-text-color;
				
				&:hover {
					color: $first-main-color;
				}

				&:after {
					content: '/';
					position: absolute;
					top: 0;
					right: -17px;
					color: $default-text-color;
				}

				&:last-child:after {
					content: '';
				}
			}
		}
	}
	
	&.widget_product_search {
		
		.woocommerce-product-search {
			@include mkdfRelativeHolderLayout();
			clear: both;
			
			> div {
				@include mkdfRelativeHolderLayout();
				box-sizing: border-box;
			}
			
			input,
			button {
				height: 48px;
				padding: 12px 0;
				font-family: inherit;
				font-weight: inherit;
				line-height: 24px;
				border-radius: 0;
				outline: 0;
				-webkit-appearance: none;
				box-sizing: border-box;
			}

			input {
				@include mkdfRelativeHolderLayout();
				padding-left: 19px;
				padding-right: 60px;
				font-size: 15px;
				color: $default-text-color;
				background-color: $additional-background-color;
				border: 1px solid transparent;
				transition: border-color .2s ease-out;
				@extend %linear-style;

				&:focus {
					color: $default-heading-color;
					background-color: $additional-background-color;
					border-color: transparent;
				}

				@include mkdfPlaceholder {
					color: inherit;
				}
			}

			button {
				position: absolute;
				top: 0;
				right: 0;
				width: 50px;
				font-size: 15px;
				text-align: center;
				color: $default-heading-color;
				background-color: transparent;
				border: 0;
				cursor: pointer;
				transition: color .2s ease-out;

				span {
					display: block;
					line-height: inherit;
					transform: scaleX(-1);

					&:before {
						display: block;
						line-height: inherit;
					}
				}

				svg {
					position: absolute;
					top: 3px;
					right: 4px;
					transform: scale(0.5);
					fill: $default-heading-color;
					transition: fill .2s ease-out;

					* {
						fill: inherit;
					}
				}

				&:hover {
					color: $default-text-color;

					svg {
						fill: $default-text-color;
					}
				}
			}
		}
	}
}

/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */